.messageDashboard {
	width: 80%;
	position: relative;
	margin-left: auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}

.messageListContainer {
	height: calc(100% - 200px);
	width: 100%;
	padding: 0% 15%;
	overflow: auto;
}


