.sideBarContainer {
	position: relative;
	width: 20%;
	background: rgba(255, 85, 85, 1);
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	color: white;
}

.sideBarHeader {
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: space-evenly;
	    justify-content: space-evenly;
	-ms-flex-align: center;
	    align-items: center;
	height: 100px;
	background: rgba(229,76,76,1);
	margin-right: -4px;
	-webkit-box-shadow: 0px 4px 10px rgba(51,51,51, 0.6);
	        box-shadow: 0px 4px 10px rgba(51,51,51, 0.6); 
	z-index: 100;
	font-size: 1.4em;
}

.username {
	text-transform: capitalize;
}

.logoutIcon {
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}

.logoutIcon:hover {
	cursor: pointer;
	-webkit-transform: scale(1.4);
	    -ms-transform: scale(1.4);
	        transform: scale(1.4);
}

.activeUserTitle {
	margin-right: -2px; 
	background: rgba(229,76,76,1);
	color: #fff;
	text-align: center;
	-webkit-box-shadow: 0px 4px 10px rgba(51,51,51, 0.6);
	        box-shadow: 0px 4px 10px rgba(51,51,51, 0.6);
	height: 75px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

.activeUsers {
	overflow: auto;
	height: calc(100% - 175px);
}

.activeUsersList {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	margin: 16px 0;
}

.logoutContainer {
	background: rgba(229,76,76,1);
	height: 75px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: space-evenly;
	    justify-content: space-evenly;
	-ms-flex-align: center;
	    align-items: center;
	font-size: 1.4em;
	color: white;
	text-align: center;
	-webkit-box-shadow: 1px -1px 10px rgba(51,51,51, 0.6);
	        box-shadow: 1px -1px 10px rgba(51,51,51, 0.6);
	margin-right: -2px;
	z-index: 100;
}


